



























































.cart-list{
    overflow-y: scroll;
    height: 80%;
    padding-bottom:70px;
}

.cart-item-name{
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.cart-item-price{
    text-align: right;
}

.summary{
    position: absolute;
    bottom: 15px;
    left: 15px;
    right: 15px;
}



