























header{
    height: 75px;
    background-color: white;
    box-shadow: 0px 2px 5px 0px rgba(89,84,89,1);
    margin-bottom: 40px;
}

.logo{
    font-weight: bold;
}

.menu{
    vertical-align:middle;
    text-align:center;
    font-weight:bold;
    height: 100%;;
    float:left;
    padding: 0px 30px;
}

.menu-active{
    text-decoration:underline;
}

.menu:hover{
    cursor:pointer;
    text-decoration:underline;
}



