

































.card{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    text-align: center;
    margin: 15px 0px;
    background-color: white;
}

.card-img{
    width: 100%;
    height: 330px;
}

.card-content{
    padding: 10px;
}

.card-bottom{
    color: white;
    background-color: #009933;
}

.card-bottom:hover{
    opacity: 0.8;
    cursor: pointer;
}

.product{
    font-weight: bold;
}

.product-name{
    text-align: left;
    font-size: 18px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.product-price{
    text-align: right;
    margin-bottom: 0px;
}



